import React, { useEffect, useState } from "react";
import Create from "./child/Create";
import Edit from "./child/Edit";
import Delete from "./child/Delete";
import ModalUID from "../../../components/ModalUID";
export default function GroupPage() {

  const [state, setState] = useState({
    modal: {
      componentAction: null,
      title: "",
      method: null
    }
  });

  var { modal } = state;
  return (
    <div className="container-fluid">
      <div style={{ display: "flex", justifyContent: "space-between" }}>
        <h4 className="h4 title_content mb-0 text-gray-800">Phân loại nhóm</h4>{" "}
        <div style={{ display: "flex" }}>
   
          <input
            id="file-excel-import"
            type="file"
            name="name"
            style={{ display: "none" }}
          />
          <button
            data-toggle="modal"
            data-target="#modalUID"
            className="btn btn-info btn-icon-split btn-sm show"
            onClick={() =>
              setState({ ...state, modal: { componentAction: <Create></Create>, title: "Thêm phân loại", method: "CREATE" } })
            }          >
            <span className="icon text-white-50">
              <i className="fas fa-plus" />
            </span>
            <span className="text">Thêm phân loại nhóm</span>
          </button>
        </div>
      </div>

      <div className="card shadow mb-4">
        <div className="card-header py-3">
        <div className="row" style={{ justifyContent: "space-between" }}>
            <form>
              <div className="input-group mb-6" style={{ padding: "0px 20px" }}>
                <input
                  type="search"
                  name="txtSearch"
                  className="form-control"
                  placeholder="Tìm theo tên phân loại"
                  defaultValue
                  style={{ maxWidth: "400px", minWidth: "300px" }}
                />
                <div className="input-group-append">
                  <button className="btn btn-primary" type="submit">
                    <i className="fa fa-search" />
                  </button>
                </div>
              </div>
            </form>
       
          </div>
        </div>
        <div className="card-body">
          <div className="table-responsive">
            <div
              id="dataTable_wrapper"
              className="dataTables_wrapper no-footer"
            >
          
              <table
                className="table no-footer dataTable"
                id="dataTable"
                width="100%"
                cellSpacing={0}
                aria-describedby="dataTable_info"
                style={{ width: "100%" }}
              >
                <thead>
                  <tr>
                    <th
                      className="sorting_disabled sorting_asc"
                      rowSpan={1}
                      colSpan={1}
                      aria-label="STT"
                    >
                      STT
                    </th>
                    <th
                      className="sorting_disabled"
                      rowSpan={1}
                      colSpan={1}
                      aria-label="Họ và tên"
                    >
                      Tên loại nhóm bài học
                    </th>
                    <th
                      className="sorting_disabled"
                      rowSpan={1}
                      colSpan={1}
                      style={{ width: "126.359px" }}
                      aria-label="Tên tài khoản"
                    >
                      Nối dung
                    </th>
                    <th
                      className="sorting_disabled"
                      rowSpan={1}
                      colSpan={1}
                      aria-label="Số điện thoại"
                    >
                      Đối tượng áp dụng
                    </th>
                    <th
                      className="sorting_disabled"
                      rowSpan={1}
                      colSpan={1}
                      aria-label="Số điện thoại"
                    >
                      Hành động
                    </th>
                  </tr>
                </thead>
                <tbody>
                  <tr className="hover-product odd">
                    <td className="sorting_1">1</td>
                    <td style={{color : "blue"}}>Hội nhập</td>
                    <td style={{minWidth : "230px"}}>
                      Các bài học bắt buộc phải hoàn thành dành cho tất cả thành
                      viên.
                    </td>
                    <td>Tất cả nhân viên.</td>
                    <td style={{ maxWidth: "150px", display: "flex" }}>
                    <button
                      name="action"
                      className="btn btn-warning btn-sm show"
                      onClick={() =>
                        setState({ ...state, modal: { componentAction: <Edit></Edit>, title: "Sửa nhóm bài học", method: "EDIT" } })
                      }
                      data-toggle="modal"
                      data-target="#modalUID"                     >
                      <i className="fa fa-edit" /> Sửa
                    </button>
                    <button
                      name="action"
                      onClick={() =>
                        setState({ ...state, modal: { componentAction: <Delete></Delete>, title: "Xóa nhóm bài học", method: "REMOVE" } })
                      }
                      data-toggle="modal"
                      data-target="#modalUID"
                      className="btn btn-danger btn-sm show"
                      style={{ marginLeft: "10px" }}
                    >
                      <i className="fa fa-trash" /> Xóa
                    </button>
                  </td>
                  </tr>
                </tbody>
              </table>
             
            </div>
          </div>
        </div>
      </div>
      <ModalUID method={modal.method} title={modal.title}> {modal.componentAction} </ModalUID>

    </div>
  );
}
