import React, { useEffect, useState } from "react";

export default function ModalCreate() {
  const [state, setState] = useState({
    name: "",
  });

  const { name } = state;


  function onChange(e) {
    var { name, value } = e.target;
    setState({ ...state, [name]: value });
  }

  return (
    <form role="form" >
      <div class="form-group">
        <label for="product_name">Tên phân loại</label>
        <input
          type="text"
          class="form-control"
          id="txtName_branch"
          placeholder="Nhập..."
          autocomplete="off"
        />
      </div>
 
      <div class="form-group">
        <label for="product_name">Nội dung</label>

        <textarea
          placeholder="Nhập câu hỏi"
          autocomplete="off"
          name="question" class="form-control" rows="2" ></textarea>
      </div>
      <div class="form-group">
        <label for="product_name">Đối tượng áp dụng</label>
        <input
          type="text"
          class="form-control"
          id="txtName_branch"
          placeholder="Nhập..."
          autocomplete="off"
        />
      </div>
    </form>
  );
}
